/*====================
PCを基本設計にする場合
- 1280px〜：大型PC
- 960px〜1279px：小型PC
- 600px〜959px：タブレット
- 480px〜599px：スマートフォン横
- 〜479px：スマートフォン縦
====================*/
/*====================
スマホを基本設計にする場合
- 〜479px：スマートフォン縦
- 480px〜599px：スマートフォン横
- 600px〜959px：タブレット
- 960px〜1279px：小型PC
- 1280px〜：大型PC
====================*/

//今回はPC
//レスポンシブ対応（スマホ）
//ブレイクポイント
$mb:767px;//スマホ
$tb:768px;//タブレット
$lt:1024px;//ラップトップ
//@mediaのフォーマット
@mixin mobile{
  @media (max-width:$mb){
    @content;
  }
}

@mixin tablet {
  @media (min-width:$tb) {
    @content;
  }
}

@mixin laptop {
  @media (min-width:$lt) {
    @content;
  }
}
